* {
    margin: 0;
    padding: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
        'Open Sans', 'Helvetica Neue', sans-serif;
}

#scroll--up--button {
    display: flex;
    position: fixed;
    bottom: 20px;
    right: 30px;
    z-index: 99;
    border: none;
    outline: none;
    background-color: #480b21;
    color: white;
    cursor: pointer;
    padding: 15px;
    border-radius: 15px;
    font-size: 1rem;
}

#scroll--up--button:hover {
    background-color: #af98a1;
    color: black;
}

.root {
    display: flex;
}

.header {
    display: flex;
    justify-content: center;
    width: 100%;
    background-color: #ab6f84;
    padding: 30px;
}

.make--title {
    color: #480b21;
    text-decoration: underline;
}

.header--buttons {
    display: flex;
    justify-content: center;
    gap: 2rem;
    margin-left: 3rem;
}

.header--buttons > button {
    background-color: transparent;
    font-weight: 600;
    font-size: medium;
    border: none;
    padding: 0 1rem;
    border-radius: 15px;
}

.general--container {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.alert--box {
    display: none;
    flex-direction: column;
    gap: 3rem;
    background-color: rgba(171, 61, 112, 0.9);
    box-shadow: 0px 2px 38px rgba(0, 0, 0, 0.5);
    position: fixed;
    top: 50%;
    left: 50%;
    padding: 90px;
    max-width: 15rem;
    transform: translate(-50%, -50%);
    z-index: 99999;
}

.alert--box.active {
    display: flex;
}

.alert--box--text {
    font-size: larger;
    font-weight: 600;
    letter-spacing: -0.1rem;
}

.alert--box--buttons {
    margin-top: 2rem;
    display: flex;
    justify-content: space-evenly;
}

.alert--box--buttons > button {
    padding: 0.3rem 0.5rem;
}

.alert--highlight {
    font-weight: 800;
    font-size: 1.2rem;
}

.cv--container {
    box-shadow: 0px 2px 38px rgba(0, 0, 0, 0.2);
    display: grid;
    align-items: center;
    grid-template-rows: repeat(minmax(min-content, max-content) + 50);
    grid-auto-rows: minmax(min-content, max-content) + 50;
    margin: 3rem;
    padding: 3rem;
    max-width: 50rem;
    gap: 4rem;
}

.cv--container.preview {
    box-shadow: 0px 2px 38px rgba(0, 0, 0, 0.2);
    font-size: smaller;
    display: grid;
    align-items: center;
    grid-template-rows: repeat(minmax(min-content, max-content) + 50);
    grid-auto-rows: minmax(min-content, max-content) + 50;
    margin: 3rem;
    padding: 3rem;
    max-width: 50rem;
    gap: 1rem;
}

.general--info,
.general--info--view,
.bottom--row {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
}

.general--info--view {
    margin-right: 7rem;
}

.profile--photo,
.general--info--inputs {
    padding-left: 2rem;
    padding-right: 2rem;
}

.general--info--element {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}

.general--info--view {
    margin-bottom: 2rem;
    font-size: 2rem;
}

.profile--photo--section {
    position: relative;
}

.profile--photo--section:hover {
    -webkit-filter: brightness(80%);
    filter: brightness(80%);
}

.profile--photo--section:hover .image--picker {
    display: flex;
    cursor: pointer;
}

.profile--photo {
    width: 20rem;
    border-radius: 15px;
}

.profile--photo--selector {
    position: absolute;
    top: 80%;
    left: 36%;
}

.profile--photo--preview {
    margin: 0 2rem;
    width: 10rem;
    border-radius: 50%;
}

#image--picker {
    display: none;
}

.image--picker {
    display: none;
    background-color: #480b21;
    color: white;
    padding: 0.5rem;
    border-radius: 15px;
}

.edit--button,
.delete--button {
    display: none;
    justify-content: center;
    align-items: center;
    position: absolute;
    left: 78%;
    background-color: #c4c3c3;
    padding: 0.3rem 0.5rem;
    border-radius: 15px;
    border: none;
    /* min-width: 5rem; */
    margin-right: 20rem;
}

.delete--button {
    left: 89%;
}

.education--element.preview .edit--button,
.work--element.preview .edit--button {
    left: 89%;
    /* min-width: 7rem; */
}

.education--element:hover .edit--button,
.work--element:hover .edit--button,
.education--element:hover .delete--button,
.work--element:hover .delete--button {
    display: flex;
    width: 10%;
    transform: translate(-20%);
}

.preview--field--title {
    color: rgb(93, 92, 92);
    color: #ab6f84;
    margin: -1rem 2rem;
    text-decoration: underline;
    font-style: italic;
    letter-spacing: 0.5rem;
    font-weight: 400;
}

.name--view {
    font-variant: small-caps;
    font-size: 2rem;
}

.email--view,
.phone--view {
    font-size: smaller;
}

input,
textarea {
    padding: 0.8rem;
}

button {
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode',
        Geneva, Verdana, sans-serif;
}

button:hover {
    cursor: pointer;
    background-color: #af98a1;
}

.general--info--inputs {
    display: flex;
    /* justify-content: center; */
    flex-direction: column;
    gap: 0.5rem;
}

.general--info--inputs > label {
    text-decoration: underline;
}

.container--input {
    position: relative;
    display: flex;
    flex-direction: column;
    text-align: start;
    align-items: flex-start;
    gap: 1rem;
    padding-left: 2rem;
    padding-right: 2rem;
    margin-bottom: 3rem;
    margin-top: 2rem;
}

.container--input > section > input,
.container--input > section > textarea {
    width: 24rem;
    max-width: 24.2rem;
    min-height: 1rem;
}

.observations--input--work {
    width: 24.3rem;
    max-width: 24.3rem;
    margin-left: 2.9rem;
}

.container--input > section > textarea .title--input {
    margin-left: 2.5rem;
}
.university--input {
    margin-left: 4.2rem;
}

.observations--input {
    margin-left: 3rem;
}

.title--input {
    margin-left: 2.5rem;
}
.university--input {
    margin-left: 4.2rem;
}

.observations--input {
    margin-left: 3rem;
}

.observations--container {
    display: flex;
    align-items: center;
}

button.education,
button.work {
    position: absolute;
    padding: 1rem;
    top: 120%;
    left: 40%;
}

.place--input {
    margin-left: 4.9rem;
}

.company--input {
    margin-left: 4.2rem;
}

.bottom--row {
    display: flex;
    justify-content: space-between;
}

.languages--inputs {
    display: flex;
    flex-direction: column;
    text-align: start;
    justify-content: center;
    gap: 1rem;
    padding-left: 2rem;
    padding-right: 2rem;
    margin-top: 1rem;
}

.language--input {
    margin-left: 4rem;
}

.languages--bottom {
    display: flex;
    gap: 2rem;
}

button.languages {
    margin-left: 2.5rem;
    padding-left: 2.7rem;
    padding-right: 2.5rem;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
}

.create--button {
    padding: 1rem;
    width: 10rem;
}

.invalid--input {
    background-color: pink;
    border: 1px solid red;
}

.education--view,
.work--view,
.languages--view {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    line-height: 1.7rem;
}

.languages--view {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}

.title--view,
.place--view,
.language--view {
    font-variant: small-caps;
    font-style: italic;
    text-decoration: underline;
}

.language--view {
    font-size: 2rem;
    font-weight: 700;
    text-decoration: none;
}

.observations--view {
    margin-top: 1rem;
    letter-spacing: -0.4px;
    line-height: 1.2rem;
}

.education--element,
.work--element,
.languages--element {
    position: relative;
    padding: 1.2rem 2rem;
    border-radius: 15px;
}

.education--element:hover,
.work--element:hover,
.languages--element:hover {
    background-color: rgb(235, 235, 235);
}

.bottom--row--preview {
    display: flex;
    justify-content: space-between;
}
